import React , {useState , useEffect} from "react";
import classNames from 'classnames';
import { InputText } from 'primereact/inputtext';
import { InputTextarea } from 'primereact/inputtextarea';
import { RutasService } from "../../service/RutasServicio";
import { Button } from 'primereact/button';
import { Dropdown } from 'primereact/dropdown';
import { Rating } from 'primereact/rating';
import { Image } from 'primereact/image';
import { Dialog } from 'primereact/dialog';
import Mapsrutas from "../maps/Mapsrutas";
import html2canvas from 'html2canvas';

const FormRuta = (props) => {
    console.log("props de form de rutas ->" , props);
    const imagen = 'images/noImage.png';
    const [product, setProduct] = useState(props.datos);
    const [loading, setLoading] = useState(false);  
    const [displayMaximizable, setDisplayMaximizable] = useState(false); 
    const [position, setPosition] = useState('center');
    const [mostrarMapa , setMostrarMapa] = useState(false);
    const [ubicaciones , setUbicaciones] = useState([]);
    const [imgMap , setImgMap] = useState(null);
    const [loadingMap , setLoadingMap] = useState(false);

    const onInputChange = (e, name) => {
        const val = (e.target && e.target.value) || '';
        let _product = { ...product };
        _product[`${name}`] = val;
        setProduct(_product);
    }


    const dialogFuncMap = {
        'displayMaximizable': setDisplayMaximizable
    }

    const status = [
        {name: 'Disponible', value: 'disponible'},
        {name: 'No disponible', value: 'no disponible'}
    ];

    const rutasService = new RutasService();

    async function newRutaRegister(){
        const response = await rutasService.newRuta(product);
        if(response.status === 200){
            setLoading(false);
            props.onSuccess();
        }
    }

    async function editRuta(){
        const response = await rutasService.updateRuta(product);
        if(response.status === 200){
            setLoading(false);
            props.onSuccess();
        }
    }

    async function cargarUbicaciones(id){
        if(id){
            const response = await rutasService.getUbicacionesByruta(id);
            if(response.status === 200){
                setUbicaciones(response.data.data);
                setMostrarMapa(true);
            }
        }else{
            setMostrarMapa(false);
        }
    }

    const accionRuta = () =>{
        setLoading(true);
        if(props.operacion === 'Crear'){
            newRutaRegister();
        }else{
            editRuta();
        }
    }

    useEffect(() => {
        if(product){
            cargarUbicaciones(product.idruta);
        }
    }, []);

    const ImageTemplate = () => {
        if(product.foto && product.foto.length > 20){
            if(imgMap){
                product.foto = imgMap;
                return  <Image  src={`${product.foto}`} alt={product.foto} width="400" height="200" preview />
            }else{
                return  <Image  src={`${product.foto}`} alt={product.foto} width="400" height="200" preview />
            }
        }
 
        if(imgMap){
            product.foto = imgMap;
            return  <Image  src={`${product.foto}`} alt={product.foto} width="400" height="200" preview />
        }

        return  <Image src={imagen} alt={product.image} width="400" height="200" />
    }

    const ProductDialogFooter = () =>{
      
        return(
            <>
                <Button style={{width:'50%'}} label="Cancelar" loading={loading} icon="pi pi-times" className="p-button-raised p-button-info p-button-text" onClick={props.onDiscard} />
                <Button style={{width:'50%'}} label="Guardar" loading={loading} icon="pi pi-check"   className="p-button-raised p-button-warning p-button-text" onClick={accionRuta} /> 
            </>
        )
        
    }

    const renderFooter = (name) => {
        return (
            <div>
                <Button label="Cerrar" icon="pi pi-times"  onClick={() => onHide(name)}  className="p-button-raised p-button-success p-button-text" />
                <Button label="Capturar" icon="pi pi-camera" loading={loadingMap} onClick={() => capturarImgMap(name)} autoFocus className="p-button-raised p-button-succes p-button-text" />
            </div>
        );
    }

    const onHide = (name) => {
        dialogFuncMap[`${name}`](false);
    }

    const onClick = (name, position) => {
        dialogFuncMap[`${name}`](true);

        if (position) {
            setPosition(position);
        }
    }

    const capturarImgMap = ()=>{
        setLoadingMap(true);
        const screenshotTarget = document.getElementById('map_canvas2');

        html2canvas(screenshotTarget, {
            useCORS: true,
            allowTaint: false,
            backgroundColor: null,
            ignoreElements: (node) => {
            return node.nodeName === 'IFRAME';
            }
        }).then((canvas) => {
            const base64image = canvas.toDataURL('image/jpg');
            setImgMap(base64image);
            setTimeout(() => {
                setLoadingMap(false);
                alert('Imagen capturada con exitó.');
            }, 3000);
        });
    }

    return (
        <>
            <div>
                <ImageTemplate />
                <div className="field">
                    {product.ubicaciones && product.ubicaciones !== '0' && <Button label="Visualizar en mapa" icon="pi pi-map" className="p-button-raised p-button-success p-button-text" onClick={() => onClick('displayMaximizable')} />}
                </div>
                <div className="field">
                    <label htmlFor="nombre">Nombre</label>
                    <InputText id="nombre" value={product.nombre} onChange={(e) => onInputChange(e, 'nombre')} required autoFocus className={classNames({ 'p-invalid':  !product.nombre })} />
                    {!product.nombre && <small className="p-invalid">Nombre es requerido.</small>}
                </div>
                <div className="field">
                    <label htmlFor="descripcion">Descripcion</label>
                    <InputTextarea id="descripcion" value={product.descripcion} onChange={(e) => onInputChange(e, 'descripcion')} required rows={3} cols={20} />
                </div>

                <div className="field">
                    <label htmlFor="cantidad">Cantidad</label>
                    <InputText id="cantidad" value={product.cantidad} onChange={(e) => onInputChange(e, 'cantidad')} required autoFocus className={classNames({ 'p-invalid':  !product.cantidad })} />
                    {!product.cantidad && <small className="p-invalid">Cantidad es requerida.</small>}
                </div>

                <div className="field">
                    <label htmlFor="status">Calificacion </label>
                    <Rating value={product.calificacion} onChange={(e) => onInputChange(e, 'calificacion')} />
                </div>
                <div>
                    <label htmlFor="status">Estatus</label>
                    <Dropdown value={product.status} options={status} onChange={(e) => onInputChange(e, 'status')} optionLabel="name" placeholder="Seleccionar status" />
                    {!product.status && <small className="p-invalid">Status es requerida.</small>}
                </div><br/>
                <div>
                    <ProductDialogFooter/>
                </div>
            </div>
            <Dialog header="Mapa" visible={displayMaximizable} maximizable modal style={{ width: '90vw' ,height: '90vh' }} footer={renderFooter('displayMaximizable')} onHide={() => onHide('displayMaximizable')}>
                <Mapsrutas ubicaciones={ubicaciones} />
            </Dialog>
        </>
    );
}

export default FormRuta;