import axios from 'axios';

const urlApi = 'https://app-821d0e52-20eb-4575-83d2-3b98397e76b2.cleverapps.io';

export class UsuarioService {

    async getUsuarios(){
        try {
            const response = await axios({
                url:`${urlApi}/usuarios/lista`,
                method:'GET'
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }

    async registrarUsuario(data){
        try {
            const response = await axios({
                url:`${urlApi}/usuarios/registrar`,
                method:'POST',
                data:data
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }

    async updateUsuario(data){
        try {
            const response = await axios({
                url:`${urlApi}/usuarios/actualizar`,
                method:'PUT',
                data:data
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }

    async eliminarUsuario(id){
        try {
            const response = await axios({
                url:`${urlApi}/usuarios/eliminar`,
                method:'DELETE',
                data:{idusuario:id}
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }

    async login(user){
        try {
            const response = await axios({
                url:`${urlApi}/usuarios/login/${user.correo}/${user.password}`,
                method:'GET'
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }

    async cambiarPassword(user){
        try {
            const response = await axios({
                url:`${urlApi}/usuarios/cambiar-password`,
                method:'PUT',
                data:user 
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }
}

