import React  from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import {useHistory} from 'react-router-dom';


export const AppTopbar = (props) => {
    const existSesion = localStorage.getItem('userExist');
    let history = useHistory();

    const cerrarSesion =()=>{
        localStorage.removeItem('userExist');
        localStorage.removeItem('userInfo');
        console.log("cerrar sesion ok ");
        history.push("/");
        
    }

    return (
        <div className={`layout-topbar ${props.disabled}`}>
            <Link to="/" className="layout-topbar-logo">
                <img src={props.layoutColorMode === 'light' ? 'assets/layout/images/banner.jpg' : 'assets/layout/images/banner.jpg'} alt="logo"/>
                <span>Aqua Nuovo</span>
            </Link>
            {existSesion != null ? 
                <>
                    <button type="button" className="p-link  layout-menu-button layout-topbar-button" onClick={props.onToggleMenuClick}>
                        <i className="pi pi-bars"/>
                    </button>
                   <button type="button" className="p-link layout-topbar-menu-button layout-topbar-button" onClick={props.onMobileTopbarMenuClick}>
                        <i className="pi pi-ellipsis-v" />
                    </button>
   
                    <ul className={classNames("layout-topbar-menu lg:flex origin-top", {'layout-topbar-menu-mobile-active': props.mobileTopbarMenuActive })}>
                        <li>
                            <button className="p-link layout-topbar-button" onClick={props.onMobileSubTopbarMenuClick}>
                                <i className="pi pi-power-off" onClick={cerrarSesion} />
                                <span>Cerrar sesion</span>
                            </button>
                        </li>
                    </ul>
                </>
            :
                <></>
            }
           
          
         
        </div>
    );
}
