import axios from 'axios';

const urlApi = 'https://app-821d0e52-20eb-4575-83d2-3b98397e76b2.cleverapps.io';

export class RutasService {

    async getRutas(){
        try {
            const response = await axios({
                url:`${urlApi}/rutas/rutas`,
                method:'GET'
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }

    async newRuta(data){
        try {
            const response = await axios({
                url:`${urlApi}/rutas/registrar-ruta`,
                method:'POST',
                data:data
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }

    async updateRuta(data){
        try {
            const response = await axios({
                url:`${urlApi}/rutas/actulizar-ruta`,
                method:'PUT',
                data:data
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }

    async eliminarRuta(id){
        try {
            const response = await axios({
                url:`${urlApi}/rutas/eliminar-ruta`,
                method:'DELETE',
                data:{idruta:id}
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }


    async newUbicaciones(data){
        try {
            const response = await axios({
                url:`${urlApi}/rutas/resgistrar-ubicaciones`,
                method:'POST',
                data:data
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }


    async getUbicacionesByruta(id){
        try {
            const response = await axios({
                url:`${urlApi}/rutas/ubicaciones/${id}`,
                method:'GET'
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }

    async getUbicacionesRutas(){
        try {
            const response = await axios({
                url:`${urlApi}/rutas/ubicaciones-rutas`,
                method:'GET'
            });
    
            return response;

        } catch (e) {
            console.log("error ->", e);
            return e;
        }
    }
}