import React, { useState, useEffect ,useRef } from 'react';
import { DataView} from 'primereact/dataview';
import { Button } from 'primereact/button';
import { Rating } from 'primereact/rating';
import { RutasService } from '../service/RutasServicio';
import { Toast } from 'primereact/toast';
import { LoadCards } from '../components/loaded/loadpageCards';
import { Dialog } from 'primereact/dialog';

const List = (props) => {

    const [layout, setLayout] = useState('grid');
 
    const [sortOrder, setSortOrder] = useState(null);
    const [sortField, setSortField] = useState(null);
    const imagen = 'images/noImage.png';
    const [loading , setLoading] = useState(true);
    const [product , setProduct] = useState(null);


    const [rutas, setRutas] = useState(null);
    const toast = useRef(null);
    const rutasService = new RutasService();


    async function loadRutas(){
        props.loading(true);
        const response = await rutasService.getRutas();

        if(response.status === 200){
            setRutas(response.data.data);
            toast.current.show({ 
                severity: 'success', 
                summary: 'Correcto', 
                detail: 'Rutas cargadas con exito.', 
                life: 3000 
            });
            setLoading(false);
            props.loading(false);
        }
    }

    useEffect(() => {
        loadRutas();
    }, []);



    const verProducto = (producto) => {
        setProduct(producto);
        onClick('displayBasic2')
    }

    const dataviewListItem = (data) => {
        let img = "";
        if(data.foto !== null){
            if(data.foto.length < 20){
                img = imagen;
            }else{
                img = data.foto;
            }
        }else{
            img = imagen;
        }
       
        
        return (
            <div className="col-12">
                <div className="flex flex-column md:flex-row align-items-center p-3 w-full">
                    <img src={`${img}`} alt={data.nombre} className="my-4 md:my-0 w-9 md:w-10rem shadow-2 mr-5" width="200" height="150" />
                    <div className="flex-1 text-center md:text-left">
                        <div className="font-bold text-2xl">{data.nombre}</div>
                        <div className="mb-3">{data.descripcion}</div>
                        <Rating value={data.calificacion} readonly cancel={false} className="mb-2"></Rating>
                        <div className="flex align-items-center">
                            <i className="pi pi-tag mr-2"></i>
                            <span className="font-semibold">Rutas</span>
                        </div>
                    </div>
                    <div className="flex flex-row md:flex-column justify-content-between w-full md:w-auto align-items-center md:align-items-end mt-5 md:mt-0">
                        <span className="text-2xl font-semibold mb-2 align-self-center md:align-self-end">Consultar con el vendedor </span>
                        <Button icon="pi pi-shopping-cart" label="Add to Cart" disabled={data.cantidad === 0} className="mb-2"></Button>
                        <span className={`product-badge status-${data.cantidad}`}>{data.cantidad}</span>
                    </div>
                </div>
            </div>
        );
    };

    const dataviewGridItem = (data) => {
        let img = "";
        if(data.foto !== null){
            if(data.foto.length < 20){
                img = imagen;
            }else{
                img = data.foto;
            }
        }else{
            img = imagen;
        }
       

        return (
            <div className="col-12 md:col-4">
                <div className="card m-3 border-1 surface-border">
                    <div className="flex align-items-center justify-content-between">
                        <div className="flex align-items-center">
                            <i className="pi pi-tag mr-2" />
                            <span className="font-semibold">Rutas</span>
                        </div>
                        <span className={`product-badge status-${data.cantidad}`}>{data.cantidad}</span>
                    </div>
                    <div className="text-center">
                        <img src={`${img}`} alt={data.nombre} className="w-9 shadow-2 my-3 mx-0" width="200" height="200" />
                        <div className="text-2xl font-bold">{data.nombre}</div>
                        {/* <div className="mb-3">{data.descripcion}</div> */}
                        <Rating value={data.calificacion} readonly cancel={false} />
                    </div>
                    <div className="flex align-items-center justify-content-between">
                        {/* <span className="text-2xl font-semibold">Consultar al vendedor</span> */}
                        <Button icon="pi pi-search" disabled={data.cantidad === 0}  onClick={() => verProducto(data)} />
                    </div>
                </div>
            </div>
        );
    };

    const itemTemplate = (data, layout) => {
        if (!data) {
            return;
        }

        if (layout === 'list') {
            return dataviewListItem(data);
        }
        else if (layout === 'grid') {
            return dataviewGridItem(data);
        }
    };

    const [displayBasic, setDisplayBasic] = useState(false);
    const [displayBasic2, setDisplayBasic2] = useState(false);
    const [displayModal, setDisplayModal] = useState(false);
    const [displayMaximizable, setDisplayMaximizable] = useState(false);
    const [displayPosition, setDisplayPosition] = useState(false);
    const [displayResponsive, setDisplayResponsive] = useState(false);
    const [position, setPosition] = useState('center');

    const dialogFuncMap = {
        'displayBasic': setDisplayBasic,
        'displayBasic2': setDisplayBasic2,
        'displayModal': setDisplayModal,
        'displayMaximizable': setDisplayMaximizable,
        'displayPosition': setDisplayPosition,
        'displayResponsive': setDisplayResponsive
    }

    const onClick = (name, position) => {
        dialogFuncMap[`${name}`](true);

        if (position) {
            setPosition(position);
        }
    }

    const onHide = (name) => {
        dialogFuncMap[`${name}`](false);
    }

    const renderFooter = (name) => {
        return (
            <div>
                <Button label="Cerrar" icon="pi pi-times" onClick={() => onHide(name)} className="p-button-text" />
            </div>
        );
    }

    return (
        <div className="grid list-demo">
            <Toast ref={toast} />
            <div className="col-12">
                {!loading ? 
                    <div className="card">
                        <h5>Rutas</h5>
                        <DataView value={rutas} layout={layout} paginator rows={9} sortOrder={sortOrder} sortField={sortField} itemTemplate={itemTemplate} /* header={dataviewHeader} */></DataView>
                    </div>
                    :
                    <><h5>Cargando....</h5><LoadCards /></>
                }
                
            </div>
            <Dialog header="Ruta" visible={displayBasic2} style={{ width: '70vw' }} footer={renderFooter('displayBasic2')} onHide={() => onHide('displayBasic2')}>
                
                {product != null ?
                   product.foto != null  ? 
                        product.foto.length < 20 ? 
                        <>  
                            <h4>{product.nombre} </h4>
                            <img src={`${imagen}`} alt={product.nombre}   width="885" height="400" />
                            <p>Descripcion:  {product.descripcion}</p>
                            <p>Cantidad: {product.cantidad}</p>
                        </>
                        :
                        <>
                            <h4>{product.nombre}</h4>
                            <img src={`${product.foto}`} alt={product.nombre}  width="885" height="400" />
                            <p>Descripcion:  {product.descripcion}</p>
                            <p> Cantidad: {product.cantidad}</p>
                        </>
                    :
                    <>
                        <img src={`${imagen}`} alt={"..."}   width="885" height="400" />
                    </>

                : 

                <>
                    <img src={`${imagen}`} alt={"..."}  width="885" height="400" />
                </>
                }
                
            </Dialog>
        </div>
    )
}


export default React.memo(List);