import React, { useState, useEffect, useRef } from 'react';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { Toast } from 'primereact/toast';
import { Button } from 'primereact/button';
import { Dialog } from 'primereact/dialog';
import { InputText } from 'primereact/inputtext';
import { LoadTable } from '../loaded/LoadedTable';
import { Image } from 'primereact/image';
import { FileUpload } from 'primereact/fileupload';
import { ProgressBar } from 'primereact/progressbar';
import { Tag }from 'primereact/tag';
import * as XLSX from 'xlsx';
import { Rating } from 'primereact/rating';


const TableComponent = (props) => {
    const indexes = props.indexes;
    const imagen = 'images/noImage.png';
    const [products, setProducts] = useState(null);
    const [deleteProductDialog, setDeleteProductDialog] = useState(false);
    const [uploadProductDialog, setUploadeProductDialog] = useState(false);
    const [product, setProduct] = useState(null);
    const [selectedProducts, setSelectedProducts] = useState(null);
    const [globalFilter, setGlobalFilter] = useState(null);
    const toast = useRef(null);
    const dt = useRef(null);
    const [data, setData] = useState(null);
    const [totalSize, setTotalSize] = useState(0);
    const fileUploadRef = useRef(null);

    useEffect(() => {
        if(props.data){
            setData(props.data);
        }
    }, [props.data]);


    const hideDeleteProductDialog = () => {
        setDeleteProductDialog(false);
    }

    const hideUploadProductDialog = () => {
        setUploadeProductDialog(false);
    }

    const confirmDeleteProduct = (product) => {
        setProduct(product);
        setDeleteProductDialog(true);
    }

    const confirmUploadProduct = (product) => {
        setProduct(product);
        setUploadeProductDialog(true);
    }

    const deleteProduct = () => {
        props.eliminar(product);
        setDeleteProductDialog(false);
        toast.current.show({ severity: 'success', summary: 'Successful', detail: 'Eliminado con exito', life: 3000 });
    }


    const imageBodyTemplate = (rowData) => {
       
        if(rowData.foto && rowData.foto.length > 20){
            return (
                <>
                    <Image  src={`${rowData.foto}`} alt={rowData.image}  width="100" height="80" preview />
                </>
            )
        }

        return (
            <>
                <Image src={imagen} alt={rowData.image}  width="100" height="80" />
            </>
        )
    }

    const calificacionTemplate = (rowData) =>{
        return  <Rating value={rowData.calificacion} readOnly  cancel={false} />
    }

    const lineColorTemplate = (rowData) => {
        return (
            <>
                <div className="mt-2 md:mt-0 flex align-items-center">
                    <div className="surface-300 border-round overflow-hidden w-10rem lg:w-6rem" style={{height: '8px'}}>
                        <div className=" h-full" style={{width: '100%' , backgroundColor:`${rowData.color}`}}/>
                    </div>
                </div>
            </>
        )
    }


    const actionBodyTemplate =(rowData) => {
        return(
            <>
                {props.optionEdit &&  <Button icon="pi pi-pencil" className="p-button-raised p-button-warning p-button-text" onClick={() =>{props.editar(rowData)}} />}
                {props.optionDelete &&   <Button  icon="pi pi-trash" className="p-button-raised p-button-danger p-button-text" onClick={() => confirmDeleteProduct(rowData)} />}
                {props.optionMD && <Button  icon="pi pi-upload" className="p-button-raised p-button-info p-button-text" onClick={() => confirmUploadProduct(rowData)}  />}
            </>
        )
    }

    const header = (
        <div className="flex flex-column md:flex-row md:justify-content-between md:align-items-center">
            <h5 className="m-0">{props.tituloPagina}</h5>
            {props. optionSearch &&
                <span className="block mt-2 md:mt-0 p-input-icon-left">
                    <i className="pi pi-search" />
                    <InputText type="search" onInput={(e) => setGlobalFilter(e.target.value)} placeholder="Buscar..." />
                </span>
            }
            {props.optionNew &&
                <span className="block mt-2 md:mt-0 p-input-icon-right" >
                    <Button label="Nuevo" icon="pi pi-plus" className="p-button-raised p-button-success p-button-text" onClick={props.nueva} />
                </span>
            }
        </div>
    );

    const deleteProductDialogFooter = (
        <>
            <Button label="No" icon="pi pi-times" className="p-button-raised p-button-info p-button-text" onClick={hideDeleteProductDialog} />
            <Button label="Si" icon="pi pi-check" className="p-button-raised p-button-danger p-button-text" onClick={deleteProduct} />
        </>
    );


    const uploadProductDialogFooter = (
        <>
            <Button label="Cancelar" icon="pi pi-times" className="p-button-raised p-button-info p-button-text" onClick={hideUploadProductDialog} />
        </>
    );

    const emptyTemplate = () => {
        return (
            <div className="flex align-items-center flex-column">
                <i className="pi pi-image mt-3 p-5" style={{'fontSize': '5em', borderRadius: '50%', backgroundColor: 'var(--surface-b)', color: 'var(--surface-d)'}}></i>
                <span style={{'fontSize': '1.2em', color: 'var(--text-color-secondary)'}} className="my-5">Drag and Drop Image Here</span>
            </div>
        )
    }

    const onTemplateSelect = (e) => {
        let _totalSize = totalSize;
        _totalSize += e.files[0].size;
        setTotalSize(_totalSize);

        let hojaXlsx = [];
        if(e.files.length > 0 ){
            let reader = new FileReader();
            reader.readAsArrayBuffer(e.files[0]);
            reader.onloadend = (file) => {
                let data = new Uint8Array(file.target.result);
                let worbook = XLSX.read(data , { type : 'array'});

                worbook.SheetNames.forEach(function(sheetName){
                    let Xl_row_object = XLSX.utils.sheet_to_row_object_array(worbook.Sheets[sheetName]);
                    hojaXlsx.push({
                        data:Xl_row_object,
                        sheetName
                    })
                });

                hojaXlsx[0].data.forEach(element => {
                    element.idruta = product.idruta;
                    element.lat = parseFloat(element.lat);
                    element.lng = parseFloat(element.lng);
                });
                setProducts(hojaXlsx[0].data);
            }
        }
    }

    const onTemplateUpload = (e) => {
        let _totalSize = 0;
        _totalSize += e.files[0].size;
        setTotalSize(_totalSize);
        if(products){
            props.registrarMD(products);
            toast.current.show({severity: 'info', summary: 'Success', detail: 'File Uploaded'});
            hideUploadProductDialog();
        }
       
    }

    const onTemplateClear = () => {
        setTotalSize(0);
    }

    const headerTemplate = (options) => {
        const { className, chooseButton, uploadButton, cancelButton } = options;
        const value = totalSize/10000;
        const formatedValue = fileUploadRef && fileUploadRef.current ? fileUploadRef.current.formatSize(totalSize) : '0 B';

        return (
            <div className={className} style={{backgroundColor: 'transparent', display: 'flex', alignItems: 'center'}}>
                {chooseButton}
                {uploadButton}
                {cancelButton}
                <ProgressBar value={value} displayValueTemplate={() => `${formatedValue} `} style={{width: '300px', height: '20px', marginLeft: 'auto'}}></ProgressBar>
            </div>
        );
    }

    const itemTemplate = (file, props) => {
        return (
            <div className="flex align-items-center flex-wrap">
                <div className="flex align-items-center" style={{width: '40%'}}>
                    <img alt={file.name} role="presentation" src={file.objectURL} width={100} />
                    <span className="flex flex-column text-left ml-3">
                        {file.name}
                        <small>{new Date().toLocaleDateString()}</small>
                    </span>
                </div>
                <Tag value={props.formatSize} severity="warning" className="px-3 py-2" />
                <Button type="button" icon="pi pi-times" className="p-button-outlined p-button-rounded p-button-danger ml-auto" onClick={() => onTemplateRemove(file, props.onRemove)} />
            </div>
        )
    }

    const chooseOptions = {icon: 'pi pi-fw pi-file-excel', iconOnly: true, className: 'custom-choose-btn p-button-rounded p-button-outlined'};
    const uploadOptions = {icon: 'pi pi-fw pi-cloud-upload', iconOnly: true, className: 'custom-upload-btn p-button-success p-button-rounded p-button-outlined'};
    const cancelOptions = {icon: 'pi pi-fw pi-times', iconOnly: true, className: 'custom-cancel-btn p-button-danger p-button-rounded p-button-outlined'};

    const onTemplateRemove = (file, callback) => {
        setTotalSize(totalSize - file.size);
        callback();
    }

    return (
        <div className="grid crud-demo">
            <div className="col-12 col-md-12 col-xs-6 ">
                <Toast ref={toast} />
                <>
                    {data && data.length > 0 ?
                    <>
                        <DataTable scrollable scrollHeight="400px" ref={dt} value={data} selection={selectedProducts} onSelectionChange={(e) => setSelectedProducts(e.value)}
                            dataKey="id" paginator rows={props.viewRow} rowsPerPageOptions={[5, 10, 25]} className="datatable-responsive"
                            paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
                            currentPageReportTemplate="Mostrando {first} a {last} de {totalRecords} "
                            globalFilter={globalFilter} emptyMessage="No se encontraron coincidencias."  header={header} responsiveLayout="scroll">
                            {indexes.map(item => 
                                <Column 
                                    key={item} 
                                    field={item.field} 
                                    header={item.header}  
                                    sortable={item.field === "foto" ? false : true} 
                                    body = {item.field === "foto" ? imageBodyTemplate :
                                            item.field === "calificacion" ? calificacionTemplate :
                                            item.field === "color" ? lineColorTemplate : ""} 
                                />
                            )}

                            {props.actionBody && <Column body={actionBodyTemplate}></Column>}
                        </DataTable>
                    </>:
                    <>
                        <h5>Cargando....</h5>
                        <LoadTable headers={indexes} ></LoadTable>
                    </>
                    }
                </>
                

                <Dialog visible={deleteProductDialog} style={{ width: '450px' }} header="Confirm" modal footer={deleteProductDialogFooter} onHide={hideDeleteProductDialog}>
                    <div className="flex align-items-center justify-content-center">
                        <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: '2rem' }} />
                        {product && <span>Estas seguro de eliminarlo <b>{product.nombre}</b>?</span>}
                    </div>
                </Dialog>

                <Dialog visible={uploadProductDialog} style={{ width: '450px' }} header="Registrar ubicaciones" modal footer={uploadProductDialogFooter} onHide={hideUploadProductDialog}>
                    <div>
                        
                        {product && <span>Cargar puntos a: <b>{product.nombre}</b></span>}

                        <FileUpload ref={fileUploadRef} name="demo[]" url='http://localhost:3005/rutas/servicioFake' multiple accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" maxFileSize={1000000}
                        onUpload={onTemplateUpload} onSelect={onTemplateSelect} onError={onTemplateClear} onClear={onTemplateClear}
                        headerTemplate={headerTemplate} itemTemplate={itemTemplate} emptyTemplate={emptyTemplate}
                        chooseOptions={chooseOptions} uploadOptions={uploadOptions} cancelOptions={cancelOptions} />
                    </div>
                </Dialog>

            </div>
        </div>
    );
}

export default React.memo(TableComponent);