import React, { useEffect, useState} from 'react';
import GoogleMapReact from 'google-map-react';
import { RutasService } from '../../service/RutasServicio';
import TableComponent from '../table/TableComponent';
import { LoadCard } from '../loaded/LoadedPageCard';

const MapsrutasUbicaciones = (props) => {

    const tablaIndexes = [
        {field : 'color' , header : "Color"},
        {field : 'nombre' , header : "Nombre"},
        {field : 'cantidad' , header : "Cantidad"},
        {field : 'calificacion' , header : "Calificacion"},
    ];

    const [map,setMap] = useState(null);
    const [rutas ,setRutas] = useState([]);
    const [maps, setMaps] = useState(null);
    const [apiLoaded,setApiLoaded] = useState(false)
    const [defaultCenter,setDefaultCenter] = useState({ lat: 20.675897845349283, lng: -103.3375 });
    const rutasService = new RutasService();
    const [loading , setLoading] = useState(true);

    async function loadRutasUbicaciones(){
        props.loading(true);
        const response = await rutasService.getUbicacionesRutas();
        if(response.status === 200){
            setRutas(response.data.data);
            setTimeout(() => {
                props.loading(false);
                setLoading(false);
            }, 3000);
        }
    }

    const createInfoWindow = (poly,InformacionRuta) => {
        const infowindow = new maps.InfoWindow();
        maps.event.addListener(poly, 'click', function (event) {
            infowindow.setContent(InformacionRuta);
            infowindow.setPosition(event.latLng);
            infowindow.open(this.map);
        });

        maps.event.addListener(poly, 'dblclick', function (event) {
            infowindow.close();
        });
    }

    const creaRuta = () =>{
        rutas.map((ruta) => {
            let poly = new maps!.Polyline({
                path: ruta.puntos,
                strokeColor: ruta.color,
                strokeOpacity: 1.0,
                strokeWeight: 4
            });
            poly.setMap(map);

            let InformacionRuta='<div className="card">'+
                                    '<h1 class="firstHeading">'+ruta.nombre+'</h1>'+
                                    '<h5 class="secondHeading">'+ruta.status+' </h5>'+
                                    '<p>'+ruta.descripcion+' </p>'+
                                '</div>';
            createInfoWindow(poly,InformacionRuta);
        })
    }

    useEffect(() => {
        loadRutasUbicaciones();
    }, [apiLoaded]);

    const handleApiLoaded = (map, maps) => {
        setMap(map);
        setMaps(maps);
        setApiLoaded(true);
    };
    
    if (rutas.length > 0 && apiLoaded){
        creaRuta();
    }


    return (
        <div className="grid">
        {!loading ? 
            <>
            <div className="col-12 xl:col-12">
                <div className="card">
                    <h5>Rutas</h5>
                    <div id="map_canvas1" style={{ height: "80vh", width: "100%"}}>
                        <GoogleMapReact
                            key={'mapa'}
                            bootstrapURLKeys={{ key: "AIzaSyBypa6mb7ZYE3rLRLFvZ9JnEfIljdlEjVo", libraries: ["places", "drawing"] }}
                            defaultCenter={defaultCenter}
                            defaultZoom={11}
                            yesIWantToUseGoogleMapApiInternals
                            onGoogleApiLoaded={({ map, maps }) => handleApiLoaded(map, maps)}
                            options={{
                            mapTypeControl: false,
                            streetViewControl: true,
                            styles: [ { featureType: 'poi', elementType: 'labels', stylers: [{ visibility: 'on' }], }, ]
                            }} 
                        >
                        </GoogleMapReact>
                    </div>
                </div>
            </div>
            <div className="col-12 xl:col-12">
                <div className="card">
                    <TableComponent
                        indexes={tablaIndexes} 
                        data={rutas}
                        optionEdit={false}
                        optionDelete={false}
                        optionMD={false}
                        optionNew={false}
                        optionSearch={true}
                        actionBody={false}
                        viewRow={4}
                        tituloPagina={"Rutas"}
                    />
                </div>
            </div>
            </>
            :
            <>
                <div className="col-12 xl:col-12"> 
                    <h5>Cargando....</h5>
                    <LoadCard titulo={'Rutas'} />
                </div>
            </>
            }
        </div>
    )
}

export default MapsrutasUbicaciones;