import React ,{useState , useEffect} from 'react';
import {useHistory} from 'react-router-dom';

import { Divider } from 'primereact/divider';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { TabPanel, TabView } from 'primereact/tabview';
import { Image } from 'primereact/image';
import { UsuarioService } from '../service/UsuarioService';


const UserPage = (props) => {
    const [activeIndex1, setActiveIndex1] = useState(0);
    const [user , setUser ] = useState ({
        correo:'',
        password: ''
    });

    const [userNew , setUserNew ] = useState ({
        nombre:'',
        correo:'',
        telefono:'',
        tipousuario:'',
        password: ''
    });
    const userService = new UsuarioService();
    let history = useHistory();

    const onInputChangeLogin = (e, name) => {
        const val = (e.target && e.target.value) || '';
        let _user = { ...user };
        _user[`${name}`] = val;
        setUser(_user);
    }

    const onInputChangeRegister = (e, name) => {
        const val = (e.target && e.target.value) || '';
        let _user = { ...userNew };
        _user[`${name}`] = val;
        setUserNew(_user);
    }

    

    
    async function loginService () {
        props.loading(true);
        const response = await userService.login(user);
        if(response.status === 200){
            if(response.data.data !== 0){
                localStorage.setItem("userInfo" ,response.data.data[0].tipousuario);
                localStorage.setItem('userExist' , true);
                history.push("/inicio");
                props.loading(false);
            }else{
                props.loading(false);
                alert("El usuario no existe ");
            }
        }
    }

    
    async function registrarService(){
        props.loading(true);
        userNew.tipousuario = "Cliente";
        const response = await userService.registrarUsuario(userNew);
        if(response.status === 200){
            if(response.data.success === 2){
                props.loading(false);
                alert("El usuario con este correo ya existe , intenta registrando uno diferente");
            }else{
                setActiveIndex1(0);
                props.loading(false);
                alert("Inicie sesion con el usuario registrado anteriormente");
            }
        }
    }

    async function cambiarPassService(){
        props.loading(true);
        const response = await userService.cambiarPassword(user);
        if(response.status === 200){
            if(response.data.data === 1){
                props.loading(false);
                setActiveIndex1(0);
                alert("Se cambio la contraseña correctamente , por favor inicie sesion con las nuevas credenciales.");
            }else{
                props.loading(false);
                alert("No existe el correo");
            }
        }
    }
   
    const iniciarSesion = () => {
        if(user !== null){
            if(user.correo === "" || user.password === ""){
                alert("Los Campos son requeridos");
            }else{
                loginService();
            }
        }else{
            alert("Los Campos son requeridos");
        }
    }

    const registrar = () =>{
        if(userNew){
            if(userNew.correo === "" || userNew.password === "" || userNew.telefono === "" ||  userNew.nombre === ""){
                alert("Los campos son requeridos");
            }else{
                registrarService();
            }
        }else{
            alert("Los campos son requeridos");
        }
    } 

    const cambiarPassword = () => {
        if(user!== null){
            if(user.correo === "" || user.password === ""){
                alert("Los Campos son requeridos");
            }else{
                cambiarPassService();
            }
        }else{
            alert("Los Campos son requeridos");
        }
    }


    useEffect(() => {
       props.loading(false);
    }, []);

  
    return(
        <div>
            <div className="card">
                <div className="grid">
                    <div className="col-5 flex align-items-center justify-content-center">
                        <TabView activeIndex={activeIndex1} onTabChange={(e) => setActiveIndex1(e.index)}>
                            <TabPanel header="Iniciar sesion">
                                <br></br>
                                <div className="p-fluid">
                                    <div className="field">
                                        <label htmlFor="correoL">Correo</label>
                                        <InputText id="correoL" type="text" onChange={(e) => onInputChangeLogin(e, 'correo')} />
                                    </div>
                                    <div className="field">
                                        <label htmlFor="passwordL">Contraseña</label>
                                        <InputText id="passwordL" type="password" onChange={(e) => onInputChangeLogin(e, 'password')} required/>
                                    </div>
                                    <Button onClick={()=>iniciarSesion()} label="Iniciar sesion" icon="pi pi-user" className="p-button-raised p-button-text" ></Button>
                                </div>
                            </TabPanel>
                            <TabPanel header="Registrarse">
                                <br></br>
                                <div className="p-fluid">
                                    <div className="field">
                                        <label htmlFor="nombreR">Nombre</label>
                                        <InputText id="nombreR" type="text" onChange={(e) =>  onInputChangeRegister(e, 'nombre')} />
                                    </div>
                                    <div className="field">
                                        <label htmlFor="correoR">Correo</label>
                                        <InputText id="correoR" type="text" onChange={(e) =>  onInputChangeRegister(e, 'correo')} />
                                    </div>
                                    <div className="field">
                                        <label htmlFor="telefonoR">Telefono</label>
                                        <InputText id="telefonoR" type="text" onChange={(e) =>  onInputChangeRegister(e, 'telefono')} />
                                    </div>
                                    <div className="field">
                                        <label htmlFor="passwordR">Contraseña</label>
                                        <InputText id="passwordR" type="password" onChange={(e) =>  onInputChangeRegister(e, 'password')} />
                                    </div>
                                    <Button 
                                        label="Registrar" 
                                        icon="pi pi-user-plus" 
                                        className="p-button-raised p-button-success p-button-text" 
                                        onClick={() => registrar()} />
                                </div>
                            </TabPanel>
                            <TabPanel header="Cambiar contraseña ">
                                <br></br>
                                <div className="p-fluid">
                                    <div className="field">
                                        <label htmlFor="correoRS">Correo</label>
                                        <InputText id="correoRS" type="text" onChange={(e) =>  onInputChangeLogin(e, 'correo')} />
                                    </div>
                                    <div className="field">
                                        <label htmlFor="passwordRS">Password</label>
                                        <InputText id="passwordRS" type="password" onChange={(e) =>  onInputChangeLogin(e, 'password')} />
                                    </div>
                                    <Button 
                                        label="Cambiar" icon="pi pi-user-plus" 
                                        className="p-button-raised p-button-warning p-button-text" 
                                        onClick={()=>cambiarPassword()}
                                    />
                                </div>
                            </TabPanel>
                        </TabView>
                    </div>
                    <div className="col-2">
                        <Divider layout="vertical">
                        </Divider>
                    </div>
                    <div className="col-5 flex align-items-center justify-content-center">
                       <Image src="assets/layout/images/banner.jpg" alt="Image Text"  width="400" height="400" />
                    </div>
                </div>
            </div>
        </div>
    );
};

export default UserPage;