import React, { useState, useEffect, useRef } from 'react';
import { Toast } from 'primereact/toast';
import { UsuarioService } from '../service/UsuarioService';
import TableComponent from '../components/table/TableComponent';
import FormUsuario from '../components/forms/formUsuarios';
import DialogComponentByform from '../components/dialog/dialog';

const UsuariosCrud = (props) =>{

    const tablaIndexes = [
        {field : 'idusuario' , header : "#"},
        {field : 'nombre' , header : "Nombre"},
        {field : 'correo' , header : "Correo"},
        {field : 'telefono' , header : "Telefono"},
        {field : 'tipousuario' , header : "Tipo de usuario"}
    ];

    const usuarioEmpty ={
        nombre:'',
        correo:'',
        telefono:'',
        tipousuario:'',
        password: ''
    }

    const usuarioService = new UsuarioService();
    const [usuarios, setUsuarios] = useState(null);
    const toast = useRef(null);
    const {showDialog, DialogByForm} =DialogComponentByform();
    const [titulo , setTitulo] =useState('');

    async function getUsuarios () {
        props.loading(true);
        const response = await usuarioService.getUsuarios();
        if(response.status === 200){
            setUsuarios(response.data.data);
            toast.current.show({ 
                severity: 'success', 
                summary: 'Correcto', 
                detail: 'Usuarios cargados con exito.', 
                life: 3000 
            });
            props.loading(false);
        }
    }

    async function deleteUsuario(id){
        const response = await usuarioService.eliminarUsuario(id);
        if(response.status === 200){
           getUsuarios();
        }
    }

    useEffect(() => {
        getUsuarios();
    }, []);
    
    const editar = (data) =>{
		showDialog(data,'Editar');
        setTitulo("Editar");
	}

    const nuevo= () => {
        showDialog(usuarioEmpty,'Crear');
        setTitulo("Crear");
    }

    const eliminar = (usuario) => {
        deleteUsuario(usuario.idusuario);
    }

    
    const onSuccess = () => {
        toast.current.show({ severity: 'success', summary: 'Successful', detail: 'Accion ok', life: 3000 });
        getUsuarios();
	}

    const onDiscard = () => {
        toast.current.show({ severity: 'success', summary: 'Successful', detail: 'Accion cancelada', life: 3000 });
	}

    const onInfo = (messge) => {
        toast.current.show({ severity: 'success', summary: 'Successful', detail: `${messge}` , life: 3000 });
	}

    return(
        <div className="grid crud-demo">
            <div className="col-12">
                <div className="card">
                    <Toast ref={toast} />
                    <TableComponent 
                        indexes={tablaIndexes} 
                        data={usuarios}
                        optionEdit={true}
                        optionDelete={true}
                        optionMD={false}
                        editar={editar}
                        nueva={nuevo}
                        eliminar={eliminar}
                        optionNew={true}
                        optionSearch={true}
                        actionBody={true}
                        viewRow={5}
                        tituloPagina={"Usuarios"}
                    />
                    <DialogByForm
                        header={ titulo + "  Usuario"}
                        onSuccess={onSuccess}
                        onDiscard={onDiscard}
                        onInfo={onInfo}
                    >
                        <FormUsuario />
                    </DialogByForm>
                </div>
            </div>
        </div>
    );
}

export default React.memo(UsuariosCrud);