import React , {useState } from "react";
import classNames from 'classnames';
import { InputText } from 'primereact/inputtext';
import { UsuarioService } from "../../service/UsuarioService";
import { Button } from 'primereact/button';
import { Dropdown } from 'primereact/dropdown';

const FormUsuario = (props) => {
    const [product, setProduct] = useState(props.datos);
    const [loading, setLoading] = useState(false);  

    const onInputChange = (e, name) => {
        const val = (e.target && e.target.value) || '';
        let _product = { ...product };
        _product[`${name}`] = val;
        setProduct(_product);
    }

    const tipousuarios = [
        {name: 'Cliente', value: 'Cliente'},
        {name: 'Admin', value: 'Admin'},
        {name: 'Super', value: 'Super'}
    ];

    const usuarioService = new UsuarioService();

    async function registrar(){
        const response = await usuarioService.registrarUsuario(product);
        if(response.status === 200){
            if(response.data.success === 2){
                setLoading(false);
                alert("El usuario con este correo ya existe , intenta registrando uno diferente");
            }else{
                setLoading(false);
                props.onSuccess();
            }
        }
    }

    async function editar(){
        const response = await usuarioService.updateUsuario(product);
        if(response.status === 200){
            setLoading(false);
            props.onSuccess();
        }
    }

   

    const accionUsuario = () =>{
        setLoading(true);
        if(props.operacion === 'Crear'){
            registrar();
        }else{
            editar();
        }
    }


    const ProductDialogFooter = () =>{
      
        return(
            <>
                <Button style={{width:'50%'}} label="Cancelar" loading={loading} icon="pi pi-times" className="p-button-raised p-button-info p-button-text" onClick={props.onDiscard} />
                <Button style={{width:'50%'}} label="Guardar" loading={loading} icon="pi pi-check"   className="p-button-raised p-button-warning p-button-text" onClick={accionUsuario} /> 
            </>
        )
        
    }


    return (
        <>
            <div>
                <div className="field">
                    <label htmlFor="nombre">Nombre</label>
                    <InputText id="nombre" value={product.nombre} onChange={(e) => onInputChange(e, 'nombre')} required autoFocus className={classNames({ 'p-invalid':  !product.nombre })} />
                    {!product.nombre && <small className="p-invalid">Nombre es requerido.</small>}
                </div>
                <div className="field">
                    <label htmlFor="correo">Correo</label>
                    <InputText id="correo" value={product.correo} onChange={(e) => onInputChange(e, 'correo')} required autoFocus className={classNames({ 'p-invalid':  !product.correo })} />
                    {!product.correo && <small className="p-invalid">Correo es requerido.</small>}
                </div>
                <div className="field">
                    <label htmlFor="telefono">Telefono</label>
                    <InputText id="telefono" value={product.telefono} onChange={(e) => onInputChange(e, 'telefono')} required autoFocus className={classNames({ 'p-invalid':  !product.telefono })} />
                    {!product.telefono && <small className="p-invalid">telefono es requerido.</small>}
                </div>
                <div>
                    <label htmlFor="status">Tipo de usuario</label>
                    <Dropdown value={product.tipousuario} options={tipousuarios} onChange={(e) => onInputChange(e, 'tipousuario')} optionLabel="name" placeholder="Seleccionar tipo de usuario" />
                    {!product.tipousuario && <small className="p-invalid">Tipo de usuario es requerido.</small>}
                </div>
                <div className="field">
                    <label htmlFor="password">Password</label>
                    <InputText id="password" value={product.password} onChange={(e) => onInputChange(e, 'password')} required autoFocus className={classNames({ 'p-invalid':  !product.password })} />
                    {!product.password && <small className="p-invalid">Password es requerido.</small>}
                </div>
                <br/>
                <div>
                    <ProductDialogFooter/>
                </div>
            </div>
        </>
    );
}

export default FormUsuario;