import React, { useState, useEffect, useRef } from 'react';
import { Toast } from 'primereact/toast';
import { RutasService } from '../service/RutasServicio';
import TableComponent from '../components/table/TableComponent';
import DialogComponentByform from '../components/dialog/dialog';
import FormRuta from '../components/forms/formRutas';

const RutasLista = (props) => {
    
    const tablaIndexes = [
        {field : 'idruta' , header : "#"},
        {field : 'nombre' , header : "Nombre"},
        {field : 'foto' , header : "Foto"},
        {field : 'cantidad' , header : "Cantidad"},
        {field : 'status' , header : "Status"}
    ];

    const rutaEmpty ={
        nombre:'',
        descripcion:'',
        foto:'',
        cantidad:0,
        status:''
    }

    const [rutas, setRutas] = useState(null);
    const toast = useRef(null);
    const rutasService = new RutasService();
    const {showDialog, DialogByForm} =DialogComponentByform();
    const [titulo , setTitulo] =useState('');


    async function loadRutas(){
        props.loading(true);
        const response = await rutasService.getRutas();
        if(response.status === 200){
            setRutas(response.data.data);
            toast.current.show({ 
                severity: 'success', 
                summary: 'Correcto', 
                detail: 'Rutas cargadas con exito.', 
                life: 3000 
            });
            props.loading(false);
        }
    }

    async function deleteRuta(id){
        const response = await rutasService.eliminarRuta(id);
        if(response.status === 200){
           loadRutas();
        }
    }

    async function ubicacionesRuta(data) {
        const response = await rutasService.newUbicaciones(data);

        if(response.status === 200){
          loadRutas();
        }
    }

    useEffect(() => {
        loadRutas();
    }, []);

    const editarRuta = (data) =>{
		showDialog(data,'Editar');
        setTitulo("Editar");
	}

    const nuevaRuta = () => {
        showDialog(rutaEmpty,'Crear');
        setTitulo("Crear");
    }

    const eliminar = (ruta) => {
        deleteRuta(ruta.idruta);
    }

    const onSuccess = () => {
        toast.current.show({ severity: 'success', summary: 'Successful', detail: 'Accion ok', life: 3000 });
        loadRutas();
	}

    const onDiscard = () => {
        toast.current.show({ severity: 'success', summary: 'Successful', detail: 'Accion cancelada', life: 3000 });
	}

    const onInfo = (messge) => {
        toast.current.show({ severity: 'success', summary: 'Successful', detail: `${messge}` , life: 3000 });
	}

    return (
        <div className="grid crud-demo">
            <div className="col-12">
                <div className="card">
                    <Toast ref={toast} />
                    <TableComponent 
                        indexes={tablaIndexes} 
                        data={rutas}
                        optionEdit={true}
                        optionDelete={true}
                        optionMD={true}
                        editar={editarRuta}
                        nueva={nuevaRuta}
                        eliminar={eliminar}
                        registrarMD={ubicacionesRuta}
                        optionNew={true}
                        optionSearch={true}
                        actionBody={true}
                        viewRow={10}
                        tituloPagina={"Rutas"}
                    />
                        
                    <DialogByForm
                        header={ titulo + "  Ruta"}
                        onSuccess={onSuccess}
                        onDiscard={onDiscard}
                        onInfo={onInfo}
                    >
                        <FormRuta/>
                    </DialogByForm>
                </div>
            </div>
        </div>
    );
}

/* const comparisonFn = function (prevProps, nextProps) {
    return prevProps.location.pathname === nextProps.location.pathname;
};
 */
export default React.memo(RutasLista);