import React from "react";
import { Redirect, Route } from "react-router";

const RootRoute = ({ component: Component, existUser: User , loadingPage: LoadingPage , tipoUsuario: TipoUsuario, ...rest}) => {

    return (
        <Route {...rest} >
            {User == null ? 
                <Redirect to="/" /> : 
                TipoUsuario === "Super" ? 
                <Component loading={LoadingPage} /> :
                <Redirect to="/inicio" /> 
            }
        </Route>
	);
};

export default RootRoute;