import React, { useEffect, useState, useRef } from 'react';
import GoogleMapReact from 'google-map-react';

const Mapsrutas = (props) => {
    
    const [map,setMap] = useState(null);
    const [maps, setMaps] = useState(null);
    const [apiLoaded,setApiLoaded] = useState(false)
    const [defaultCenter,setDefaultCenter] = useState(props.ubicaciones[Math.floor(props.ubicaciones.length/2)]);

    const creaRuta = () =>{
        new maps!.Polyline({
            path: props.ubicaciones,
            strokeColor: '#17202A',
            strokeOpacity: 1.0,
            strokeWeight: 2
        }).setMap(map);
    }

    const handleApiLoaded = (map, maps) => {
        // use map and maps objects
        setMap(map);
        setMaps(maps);
        setApiLoaded(true);
       
    };
    
 
    if (props.ubicaciones && apiLoaded){
        if (props.ubicaciones.length>0){
            map.setCenter(props.ubicaciones[Math.floor(props.ubicaciones.length/2)]);
        }
        creaRuta();
    }

    return (
        <div id="map_canvas2" style={{ height: "80vh", width: "100%"}}>
          <GoogleMapReact
            key={'mapa'}
            bootstrapURLKeys={{ key: "AIzaSyBypa6mb7ZYE3rLRLFvZ9JnEfIljdlEjVo", libraries: ["places", "drawing"] }}
            defaultCenter={defaultCenter}
            defaultZoom={11}
            yesIWantToUseGoogleMapApiInternals
            onGoogleApiLoaded={({ map, maps }) => handleApiLoaded(map, maps)}
            options={{
              mapTypeControl: false,
              streetViewControl: true,
              styles: [ { featureType: 'poi', elementType: 'labels', stylers: [{ visibility: 'on' }], }, ]
            }} 
          >
          </GoogleMapReact>
      </div>
    )
}

export default Mapsrutas;