import React from "react";
import { Redirect, Route } from "react-router";

const PublicRoute = ({ component: Component, existUser: User , loadingPage: LoadingPage , ...rest}) => {
    return (
        <Route {...rest} >
            {User == null ? <Redirect to="/" /> : <Component loading={LoadingPage} />}
        </Route>
	);
};

export default PublicRoute;