
import React from 'react';
import { Skeleton } from 'primereact/skeleton';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import './Skeleton.css';

export const LoadTable = (props) => {
    const headers = props.headers;
   
    const products = Array.from({ length: 10});

    const bodyTemplate = () => {
        return <Skeleton></Skeleton>
    }

    return (
        <div className="col-12 col-md-12 col-xs-6 ">
            <DataTable value={products} className="datatable-responsive" responsiveLayout="scroll">
                {headers.map(item =>
                    <Column key={item} field={item.field} header={item.header} style={{ width: '25%' }} body={bodyTemplate}></Column>
                )}
            </DataTable>
        </div>
    );
}
             

