import React, { useState, useEffect, useRef } from 'react';
import classNames from 'classnames';
import { Route, useLocation , Redirect } from 'react-router-dom';
import { CSSTransition } from 'react-transition-group';

import { AppTopbar } from './AppTopbar';
import { AppFooter } from './AppFooter';
import { AppMenu } from './AppMenu';
import { AppConfig } from './AppConfig';


import RutasLista from './pages/Rutas'
import UsuariosCrud from './pages/Usuarios';
import List from './pages/ListRutasMap';

import PrivateRoute from './PrivateRoute';
import PublicRoute from './PublicRoute';
import RootRoute from './RootRoute';

import PrimeReact from 'primereact/api';
import { Tooltip } from 'primereact/tooltip';

import 'primereact/resources/primereact.css';
import 'primeicons/primeicons.css';
import 'primeflex/primeflex.css';
import 'prismjs/themes/prism-coy.css';
import './assets/demo/flags/flags.css';
import './assets/demo/Demos.scss';
import './assets/layout/layout.scss';
import './App.scss';
import MapsrutasUbicaciones from './components/maps/MapsrutasUbicaciones';
import { LoadPage } from './components/loaded/LoadedPage';
import { ProgressBar } from 'primereact/progressbar';
import UserPage from './pages/Login';

const App = () => {
    const [layoutMode, setLayoutMode] = useState('overlay');
    const [layoutColorMode, setLayoutColorMode] = useState('light')
    const [inputStyle, setInputStyle] = useState('outlined');
    const [ripple, setRipple] = useState(true);
    const [staticMenuInactive, setStaticMenuInactive] = useState(false);
    const [overlayMenuActive, setOverlayMenuActive] = useState(false);
    const [mobileMenuActive, setMobileMenuActive] = useState(false);
    const [mobileTopbarMenuActive, setMobileTopbarMenuActive] = useState(false);
    const copyTooltipRef = useRef();
    const location = useLocation();
    const [disabled , setDisabled] = useState('is-disabled');
    const existUser =  localStorage.getItem('userExist');
    const tipoUsuario =  localStorage.getItem('userInfo');
    const [login , setLogin] = useState(false);
    PrimeReact.ripple = true;
    let menuClick = false;
    let mobileTopbarMenuClick = false;

    useEffect(() => {
        if(existUser !== null){
            setLogin(true);
        }
    }, [existUser]);
    useEffect(() => {

        if (mobileMenuActive) {
            addClass(document.body, "body-overflow-hidden");
        } else {
            removeClass(document.body, "body-overflow-hidden");
        }
    }, [mobileMenuActive]);

    useEffect(() => {
        copyTooltipRef && copyTooltipRef.current && copyTooltipRef.current.updateTargetEvents();
    }, [location]);

    useEffect(() => {
        window.process = {
          ...window.process,
        };
      }, []);

    const onInputStyleChange = (inputStyle) => {
        setInputStyle(inputStyle);
    }

    const onRipple = (e) => {
        PrimeReact.ripple = e.value;
        setRipple(e.value)
    }

    const onLayoutModeChange = (mode) => {
        setLayoutMode(mode)
    }

    const onColorModeChange = (mode) => {
        setLayoutColorMode(mode)
    }

    const onWrapperClick = (event) => {

        if (!menuClick) {
            setOverlayMenuActive(false);
            setMobileMenuActive(false);
        }

        if (!mobileTopbarMenuClick) {
            setMobileTopbarMenuActive(false);
        }

        mobileTopbarMenuClick = false;
        menuClick = false;
    }

    const onToggleMenuClick = (event) => {
        menuClick = true;

        if (isDesktop()) {
            if (layoutMode === 'overlay') {
                if (mobileMenuActive === true) {
                    setOverlayMenuActive(true);
                }

                setOverlayMenuActive((prevState) => !prevState);
                setMobileMenuActive(false);
            }
            else if (layoutMode === 'static') {
                setStaticMenuInactive((prevState) => !prevState);
            }
        }
        else {
            setMobileMenuActive((prevState) => !prevState);
        }

        event.preventDefault();
    }

    const onSidebarClick = () => {
        menuClick = true;
    }

    const onMobileTopbarMenuClick = (event) => {
        mobileTopbarMenuClick = true;

        setMobileTopbarMenuActive((prevState) => !prevState);
        event.preventDefault();
    }

    const onMobileSubTopbarMenuClick = (event) => {
        mobileTopbarMenuClick = true;

        event.preventDefault();
    }

    const onMenuItemClick = (event) => {
        if (!event.item.items) {
            setOverlayMenuActive(false);
            setMobileMenuActive(false);
        }
    }
    const isDesktop = () => {
        return window.innerWidth >= 992;
    }

    let menu = [] ;
    if(tipoUsuario != null){
        if(tipoUsuario === "Cliente") {

            menu =[{
                label: 'PRICIPAL',
                items: [{
                    label: 'Inicio', icon: 'pi pi-home', to: '/'
                }]
            },
            {
                label: 'ECOMMERCE', icon: 'pi pi-fw pi-sitemap',
                items: [
                    { label: 'Lista de rutas', icon: 'pi pi-shopping-cart', to: '/lista' },
                ]
            }];
        } else if( tipoUsuario === "Admin") {
            menu =[{
                label: 'PRICIPAL',
                items: [{
                    label: 'Inicio', icon: 'pi pi-home', to: '/'
                }]
            },
            {
                label: 'ECOMMERCE', icon: 'pi pi-fw pi-sitemap',
                items: [
                    { label: 'Lista de rutas', icon: 'pi pi-shopping-cart', to: '/lista' },
                ]
            },
            {
                label: 'ADMINISTRADOR', icon: 'pi pi-fw pi-clone',
                items: [
                    { label: 'Rutas', icon: 'pi pi-map', to: '/rutas' },
                ]
            }];
        } else if(tipoUsuario === "Super") {
            menu =[{
                label: 'PRICIPAL',
                items: [{
                    label: 'Inicio', icon: 'pi pi-home', to: '/'
                }]
            },
            {
                label: 'ECOMMERCE', icon: 'pi pi-fw pi-sitemap',
                items: [
                    { label: 'Lista de rutas', icon: 'pi pi-shopping-cart', to: '/lista' },
                ]
            },
            {
                label: 'ADMINISTRADOR', icon: 'pi pi-fw pi-clone',
                items: [
                    { label: 'Rutas', icon: 'pi pi-map', to: '/rutas' },
                    { label: 'Usuarios', icon: 'pi pi-users', to: '/usuarios' },
                ]
            }];
        }
    }
   

    const addClass = (element, className) => {
        if (element.classList)
            element.classList.add(className);
        else
            element.className += ' ' + className;
    }

    const removeClass = (element, className) => {
        if (element.classList)
            element.classList.remove(className);
        else
            element.className = element.className.replace(new RegExp('(^|\\b)' + className.split(' ').join('|') + '(\\b|$)', 'gi'), ' ');
    }

    const wrapperClass = classNames('layout-wrapper', {
        'layout-overlay': layoutMode === 'overlay',
        'layout-static': layoutMode === 'static',
        'layout-static-sidebar-inactive': staticMenuInactive && layoutMode === 'static',
        'layout-overlay-sidebar-active': overlayMenuActive && layoutMode === 'overlay',
        'layout-mobile-sidebar-active': mobileMenuActive,
        'p-input-filled': inputStyle === 'filled',
        'p-ripple-disabled': ripple === false,
        'layout-theme-light': layoutColorMode === 'light',
        'pointer-events': 'none' , 
        'opacity': 0.7
    });


    const loadingPage = (loading) => {
        if(loading){
            setDisabled('is-disabled');
        }else{
            setDisabled('');
        }
       
    }

    return (
        <div className={wrapperClass } onClick={onWrapperClick}  >
            <LoadPage />
            <Tooltip ref={copyTooltipRef} target=".block-action-copy" position="bottom" content="Copied to clipboard" event="focus" />

            <AppTopbar 
                onToggleMenuClick={onToggleMenuClick} 
                layoutColorMode={layoutColorMode}
                mobileTopbarMenuActive={mobileTopbarMenuActive} 
                onMobileTopbarMenuClick={onMobileTopbarMenuClick} 
                onMobileSubTopbarMenuClick={onMobileSubTopbarMenuClick} 
                disabled={disabled} 
                login={login}
                loading={loadingPage}
            />

            <div className={`layout-sidebar ${disabled}`} onClick={onSidebarClick} >
                <AppMenu model={menu} onMenuItemClick={onMenuItemClick} layoutColorMode={layoutColorMode} onToggleMenuClick={onToggleMenuClick} />
            </div>
            <ProgressBar mode="indeterminate" style={{height : '4px'}} />
            <div className={`layout-main-container ${disabled}`} >
                <div className="layout-main">
                    <Route path="/"  exact >
                        {existUser != null ? 
                            <Redirect to="/inicio" /> : 
                            <UserPage loading={loadingPage}  />
                        }
                    </Route>
                    <PublicRoute 
                        path="/inicio"  
                        exact={true}
                        existUser={existUser}
                        loadingPage={loadingPage}
                        component={MapsrutasUbicaciones}
                    />
                    <PublicRoute 
                        path="/lista"  
                        exact
                        existUser={existUser}
                        loadingPage={loadingPage}
                        component={List}
                    />
                    <PrivateRoute
                        path="/rutas"  
                        exact
                        tipoUsuario={tipoUsuario}
                        existUser={existUser}
                        loadingPage={loadingPage}
                        component={RutasLista}
                    />
                     <RootRoute
                        path="/usuarios"  
                        exact
                        tipoUsuario={tipoUsuario}
                        existUser={existUser}
                        loadingPage={loadingPage}
                        component={UsuariosCrud}
                    />
                </div>

                <AppFooter layoutColorMode={layoutColorMode} />
            </div>

            <AppConfig className="btn-float-page" rippleEffect={ripple} onRippleEffect={onRipple} inputStyle={inputStyle} onInputStyleChange={onInputStyleChange}
                layoutMode={layoutMode} onLayoutModeChange={onLayoutModeChange} layoutColorMode={layoutColorMode} onColorModeChange={onColorModeChange} />

            <CSSTransition classNames="layout-mask" timeout={{ enter: 200, exit: 200 }} in={mobileMenuActive} unmountOnExit>
                <div className="layout-mask p-component-overlay"></div>
            </CSSTransition>

        </div>
    );

}

export default App;
