import React from "react";
import { Redirect, Route } from "react-router";

const PrivateRoute = ({ component: Component, existUser: User , loadingPage: LoadingPage ,tipoUsuario: TipoUsuario, ...rest}) => {

    return (
        <Route {...rest} >
            {User == null ? 
                <Redirect to="/" /> : 
                    TipoUsuario === "Super"  || TipoUsuario === "Admin" ? 
                    <Component loading={LoadingPage} /> : 
                    <Redirect to="/inicio" /> 
            }
        </Route>
	);
};

export default PrivateRoute;