import React, { useState } from "react";
import { Dialog } from "primereact/dialog";


const DialogComponentByform = () => {
	const [visible, setVisible] = useState(false);
	const [data, setData] = useState();
	const [op, setOp] = useState();

	const showDialog = (data, operacion, onSuccess, onDiscard) => {
		setData(data);
		setOp(operacion);
		setVisible(true);
		return <DialogByForm data={data} operacion={op} onSuccess={onSuccess} onDiscard={onDiscard} />;
	};



	const DialogByForm = props => {
		return (
			<Dialog
				{...props}
				header={props.header || "Header"}
				iconsTemplate={props.icon}
				visible={visible}
				style={{ width: "450px" }}
				modal={true}
                className="p-fluid"
				onHide={() => {
					setVisible(false);
				}}>
				{React.cloneElement(props.children, {
					operacion: op,
					datos: data,
					onSuccess: () => {
						setVisible(false);
						props.onSuccess();
					},
					onDiscard: () => {
						setVisible(false);
						props.onDiscard();
					}
				})}
			</Dialog>
		);
	};

	return { showDialog, DialogByForm };
};

export default DialogComponentByform ;